.sidebar {
  width: 20%;
  outline: 0px solid red;
  min-height: 300px;
  background: white;
  border: 1px solid silver;
  border-radius: 10px;
  .sidebarBottom {
    .userPhoto {
      outline: 0px solid red;
      margin-top: -60px;
      width: 100%;
      text-align: center;
    }
    .userName {
      outline: 0px solid green;
      font-size: 18px;
      text-align: center;
      font-weight: 600;
      margin: 10px 0 0 0;
      a{
        color: black;
      }
    }
    .viewP {
      text-align: center;
      margin: 30px 0 20px 0;
    }
  }
  .sidebarTop {
    background-color: #A0B4B7;  // #d55845
    width: 100%;
    height: 100px;
  }
  @media screen and (max-width: 1500px) {
      display: none;
  } 
}
