.friend {
    text-align: center;
    margin: 10px 0 0 0;
    outline: 0px solid red;
    display: flex;
    justify-content: space-between;
    .imgAndName {
        display: flex;
        align-items: center;
    }
    img {
        border-radius: 50%;
        height: 50px;
        width: 50px;
        cursor: pointer;
    }
    .nameLink {
        margin: 0 0 0 10px;
        cursor: pointer;
        a {
            color: black;
            font-weight: 400;
            &:hover {
                font-weight: 300;
            }
        }
    }
    button {
        align-self: center;
        cursor: pointer;
    }
}
